import React from 'react'
import Link from 'gatsby-link'

const Menu = (props) => (
    <nav id="menu">
        <div className="inner">
            <ul className="links">
                <li><Link onClick={props.onToggleMenu} to="/">Home</Link></li>
                <li><Link onClick={props.onToggleMenu} to="/listen">Listen</Link></li>
                <li><Link onClick={props.onToggleMenu} to="/events">Events</Link></li>
                <li><Link onClick={props.onToggleMenu} to="/ministry">Ministry</Link></li>
                <li><Link onClick={props.onToggleMenu} to="/attend">Attend a Service</Link></li>
                <li><Link onClick={props.onToggleMenu} to="/contact">Contact Us</Link></li>
            </ul>
            <ul className="actions vertical">
                <li><a href="/newhere" className="button special fit">New Here?</a></li>
            </ul>
        </div>
        <a className="close" onClick={props.onToggleMenu} href="javascript:;">Close</a>
    </nav>
)

Menu.propTypes = {
    onToggleMenu: React.PropTypes.func
}

export default Menu



