import React from 'react'

const Footer = (props) => (
    <footer id="footer">
        <div className="inner">
            <ul className="icons">
                <li><a href="https://twitter.com/myffch" className="icon alt fa-twitter"><span className="label">Twitter</span></a></li>
                <li><a href="https://facebook.com/myffch" className="icon alt fa-facebook"><span className="label">Facebook</span></a></li>
                <li><a href="https://instagram.com/myffch" className="icon alt fa-instagram"><span className="label">Instagram</span></a></li>
            </ul>
            <ul className="copyright">
                <li>&copy; 2018</li><li>Design: <a href="https://myffc.org">Faith Family Church, Taylors, SC</a></li>
            </ul>
        </div>
    </footer>
)

export default Footer



